"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { motion, AnimatePresence } from "framer-motion";
import { Switch } from "@heroui/react";
import { Moon, Sun } from "lucide-react";

export default function Header() {
  const router = useRouter();
  const [openMenu, setOpenMenu] = useState<string | null>(null);

  const toggleMenu = (menu: string) => {
    setOpenMenu(openMenu === menu ? null : menu);
  };

  const submenuVariants = {
    hidden: { opacity: 0, y: -10 },
    visible: { opacity: 1, y: 0 },
  };

  const submenuItemStyle: React.CSSProperties = {
    padding: "0.5rem 0.75rem",
    cursor: "pointer",
    borderRadius: "0.375rem",
    transition: "all 0.2s ease",
    color: "white",
  };

  return (
    <header className="w-full fixed top-0 left-0 z-50">
      {/* ðŸ”¹ Topbar */}
      <div className="bg-primary text-white">
        <div className="max-w-7xl mx-auto px-4 flex items-center justify-between h-10">
          {/* Left Menus */}
          <div className="flex space-x-4">
            {["About Us", "Newsroom", "Careers", "Blog", "Complaints"].map((item) => (
              <button
                key={item}
                className="hover:underline transition text-sm md:text-base lg:text-sm xl:text-base text-xs sm:text-sm"
              >
                {item}
              </button>
            ))}
          </div>

          {/* Right - Portals Dropdown */}
          <div style={{ position: "relative" }}>
            <button
              onClick={() => toggleMenu("portals")}
              style={{
                backgroundColor: "#DC2626",
                color: "white",
                fontWeight: 600,
                padding: "0.3rem 0.8rem",
                borderRadius: "0.375rem",
                border: "none",
                cursor: "pointer",
                transition: "all 0.3s ease",
                fontSize: "0.875rem",
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.backgroundColor = "white";
                e.currentTarget.style.color = "#DC2626";
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.backgroundColor = "#DC2626";
                e.currentTarget.style.color = "white";
              }}
            >
              Portals
            </button>

            <AnimatePresence>
              {openMenu === "portals" && (
                <motion.div
                  initial="hidden"
                  animate="visible"
                  exit="hidden"
                  variants={submenuVariants}
                  style={{
                    position: "absolute",
                    right: 0,
                    top: "100%",
                    zIndex: 1000,
                    background: "#2563EB",
                    borderRadius: "0.5rem",
                    padding: "0.5rem",
                    minWidth: "220px",
                    boxShadow: "0 5px 15px rgba(0,0,0,0.2)",
                  }}
                >
                  {[
                    { name: "Agent Portal", path: "/agent-portal" },
                    { name: "Broker Portal", path: "/broker-portal" },
                    { name: "Provider Portal", path: "/provider-portal" },
                    { name: "Intermediary Registration", path: "/intermediary-registration" },
                    { name: "Agent Registration", path: "/agent-registration" },
                    { name: "Group Registration", path: "/group-registration" },
                  ].map((item) => (
                    <div
                      key={item.name}
                      style={submenuItemStyle}
                      onClick={() => {
                        router.push(item.path);
                        setOpenMenu(null);
                      }}
                      onMouseEnter={(e) => (e.currentTarget.style.background = "#1D4ED8")}
                      onMouseLeave={(e) => (e.currentTarget.style.background = "transparent")}
                    >
                      {item.name}
                    </div>
                  ))}
                </motion.div>
              )}
            </AnimatePresence>
          </div>
        </div>
      </div>

      {/* ðŸ”¹ Navbar */}
      <nav className="bg-danger text-white">
        <div className="max-w-7xl mx-auto px-4 h-14 flex items-center justify-between">
          {/* Brand */}
          <div
            className="font-bold text-lg cursor-pointer"
            onClick={() => router.push("/")}
          >
            Birdview Microinsurance
          </div>

          {/* Search + Retrieve */}
          <div className="flex items-center gap-2">
            <input
              type="text"
              placeholder="Search..."
              className="px-2 py-1 rounded text-black text-sm"
            />
            <input
              type="text"
              placeholder="Retrieve Policy"
              className="px-2 py-1 rounded text-black text-sm"
            />
          </div>

          {/* Theme Switch */}
          <Switch
            size="sm"
            color="secondary"
            startContent={<Sun className="h-4 w-4" />}
            endContent={<Moon className="h-4 w-4" />}
          />
        </div>
      </nav>
    </header>
  );
}
